/* Chat page design */

.chatPage {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 30px;
    min-height: 100vh;
}

* {
    font-family: 'Open Sans', sans-serif;
}

.container {
    position: relative;
    height: 80vh;
    display: flex;
    padding: 0;
    box-sizing: border-box;
    border: 2px solid rgba(128, 128, 128, 0.5);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.container .left_side {
    flex: 30%;
    display: flex;
    flex-direction: column;
    /* Add a light gray border */
    border-right: 2px solid rgb(192, 191, 191);
    width: 30%
}

.left_side {
    max-width: 30%;
}

.container .right_side {
    position: relative;
    flex: 70%;
    display: flex;
    flex-direction: column;
    background: wheat;
    width: 70%;
}

.container .right_side::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: url("leafs.JPG") no-repeat center center;
    opacity: 0.2;
    background-size: cover;
}

.header {
    position: relative;
    height: 75px;
    background: rgb(226, 173, 93);
    display: flex;
    align-items: center;
    padding: 12px 15px;
}

.user_image {
    position: relative;
    width: 50px;
    height: 50px;
    overflow: hidden;
    border-radius: 50%;
    cursor: pointer;
}

.user_name {
    padding-left: 10px;
    flex: 1;
    font-weight: 600;
}

.icons {
    margin-top: 20px;
    /* Increase the font-size value to make the icon bigger */
    font-size: 30px;
    cursor: pointer;
}

.emojis {
    width: 40px;
    height: 40px;
}

.bi {
    padding-left: 10px;
}

.cover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/* Search chat container */
.search_chat {
    position: relative;
    background: gray;
}

/* Search icon */
#search_icon {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    font-size: 20px;
}

.chat_list {
    background-color: white;
    /* Add a scrollbar when needed */
    overflow-y: auto;
    padding-left: 0;
    margin-bottom: 0;
}

.img_friend {
    position: relative;
    width: 60px;
    height: 60px;
    overflow: hidden;
    border-radius: 50%;
}

.friend_block {
    display: flex;
    padding: 5px 10px;
    border-bottom: 2px solid rgb(192, 191, 191);
    cursor: pointer;
    align-items: center;
}

.details {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    margin-left: 15px;
}

.friend_name {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
    font-weight: 600;
}

#add-friend {
    margin: 0;
}

.time_date {
    font-size: 0.75em;
    font-weight: 600;
    color: lightgray;
    text-align: right;
}

.last_message {
    color: gray;
    font-size: 0.9em;
    display: flex;
    justify-content: space-between;
    align-items: center;
    word-wrap: break-word;
    white-space: pre-wrap;
}

