.friend_block:hover {
    background-color: rgb(245, 242, 242);
}

.user_name span {
    font-weight: 500;
    line-height: 1.2em;
    font-size: 0.8em;
    color: gray;
}

.chat_box {
    flex-grow: 1;
    overflow-y: auto;
    position: relative;
    bottom: 0;
    display: flex;
    min-width: 100%;
    flex-direction: column;
}

.message_container {
    display: flex;
    flex-direction: column;
    gap: 10px;
    overflow-y: auto;
    padding: 15px;
    white-space: pre-wrap;
}

.chat_input {
    padding-left: 15px;
}

.send_button {
    font-size: 1.1em;
    margin: 0 5px;
}

.chat_icons {
    display: flex;
    align-items: center;
    gap: 5px;
    margin-right: 5px;
    padding-left: 10px;
    font-size: 20px;
    cursor: pointer;
}

.chat_input_container {
    position: relative;
    display: flex;
    align-items: center;
    background-color: white;
    border-top: 1px solid rgb(192, 191, 191);
    width: 100%;
}

.message {
    max-width: 70%;
    padding: 8px 15px;
    border-radius: 10px;
    margin-bottom: 10px;
    word-wrap: break-word;
}

.my_message {
    background-color: rgb(172, 202, 161);
    align-self: flex-start;
    position: relative;
    padding-bottom: 2px;
}

.friend_message {
    background-color: #f1f1f1;
    align-self: flex-end;
    position: relative;
    padding-bottom: 2px;
}

.message_time {
    position: unset;
    font-size: 10px;
    color: gray;
}

.my_message .message_time {
    bottom: -15px;
    left: 15px;
}

.friend_message .message_time {
    bottom: -15px;
    right: 15px;
}

.form-control {
    border-radius: unset;
    padding-left: 40px;
}

#send-message-btn {
    margin: 5px;
    padding: 4px 18px 3px 10px;
    align-items: center;
    background-color: rgb(117, 163, 114);
    border-color: rgb(117, 163, 114);
}

#add-friend-btn {
    background-color: rgb(89, 133, 86);
    border-color: rgb(89, 133, 86);
}
